import type { Meta, StoryFn } from '@storybook/web-components';

import { html } from 'lit';

import { ButtonSize, ButtonVariant } from '@tiger-analytics/ui/button';

export default {
  title: 'Button/ta-button',
  component: 'ta-button',
  parameters: {
    controls: { expanded: true },
  },
  argTypes: {
    id: {
      description: 'Mandatory - unique id for testing',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    variant: {
      options: [ButtonVariant.Primary, ButtonVariant.Secondary, ButtonVariant.Destruct],
      description: 'different styles of the button',
      table: {
        defaultValue: { summary: ButtonVariant.Primary },
      },
      control: { type: 'select' },
    },
    size: {
      options: [ButtonSize.Default, ButtonSize.Small],
      description: 'size of the button',
      table: {
        defaultValue: { summary: ButtonSize.Default },
      },
      control: { type: 'select' },
    },
    type: {
      options: ['button', 'submit', 'reset', 'menu'],
      description: 'type of the button',
      table: {
        defaultValue: { summary: 'button' },
      },
      control: { type: 'select' },
    },
    ariaLabel: {
      description: 'A11Y - screen reader label for the button',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    iconOnly: {
      description:
        'determines if the button contains only icon. ariaLabel is mandatory if iconOnly is true.',
      table: {
        defaultValue: { summary: 'false' },
      },
      control: 'boolean',
    },
    disabled: {
      description: 'enable or disable the button',
      table: {
        defaultValue: { summary: 'false' },
      },
      control: 'boolean',
    },
    click: {
      description: 'Triggered when the button is clicked',
      table: {
        category: 'Events',
      },
      control: false,
    },
    focus: {
      description: 'Focuses the button',
      table: {
        category: 'Methods',
      },
      control: false,
    },
  },
} as Meta;

const description = `The ta-button component is a versatile and customizable button designed to fit various UI needs within your design system. It currently supports multiple variants and sizes, offering flexibility in adapting to different contexts and user interactions. Future updates will include additional features, such as icon support and new button types, allowing for even greater customization and functionality.`;

const Template: StoryFn = (args) => {
  const buttonClickHandler = () => {
    alert('Button clicked!');
  };

  if (args.iconOnly) {
    return html`
      <ta-button
        id="${args.id}"
        .variant="${args.variant}"
        .size="${args.size}"
        ?disabled="${args.disabled}"
        @click="${buttonClickHandler}"
        ?iconOnly="${args.iconOnly}"
        ariaLabel="${args.ariaLabel}"
        ><ta-settings-icon></ta-settings-icon
      ></ta-button>
      <ta-grid-container .showSideMargin="${false}">
        <ta-grid-column
          ><div class="story-title">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div></ta-grid-column
        >
        <ta-grid-column><div class="story-description">${description}</div></ta-grid-column>
      </ta-grid-container>
    `;
  }

  return html`
    <ta-button
      id="${args.id}"
      .variant="${args.variant}"
      .size="${args.size}"
      ?disabled="${args.disabled}"
      @click="${buttonClickHandler}"
      ariaLabel="${args.ariaLabel}"
      >Settings</ta-button
    >
    <ta-grid-container .showSideMargin="${false}">
      <ta-grid-column
        ><div class="story-title">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div></ta-grid-column
      >
      <ta-grid-column><div class="story-description">${description}</div></ta-grid-column>
    </ta-grid-container>
  `;
};

export const CodeAndAccessibility = Template.bind({});
CodeAndAccessibility.args = {
  id: 'test-button',
  variant: ButtonVariant.Primary,
  size: ButtonSize.Default,
  type: 'button',
  ariaLabel: 'Settings',
  iconOnly: false,
  disabled: false,
};
